<template>
  <div id="app" class="page">
    <HeaderMain/>
    <div class="main">
      <img src="@/assets/main__bg.webp" alt="bg" class="main__bg">
      <div class="container">
        <div class="main__content">
          <div class="main__input">
            <div class="main__input-wrap">
              <input type="text" placeholder=" " v-model="word">
              <label>Введите фразу для гравировки</label>
            </div>
            <a href.prevent="#" @click="setWord()">Посмотреть</a>
          </div>
        </div>
      </div>
    </div>
    <div class="result">
      <div class="container">
        <h2 class="result__title">Варианты шрифтов</h2>
        <div class="result__list">
          <div 
            v-for="index in 15"
            class="result__item"
            :key="index"
          >
            <p :class="`item-${index}`">
              {{ `${index}. ${result}` }}
            </p>
          </div>
        </div>
      </div>
      
    </div>
  </div>
</template>

<script>
import HeaderMain from './components/HeaderMain.vue';

export default {
  name: 'App',
  components: {
    HeaderMain
  },
  data(){
    return{
      word: '',
      result: 'Ваша надпись'
    }
  },
  methods:{
    setWord(){
      const text = this.word.replace(/^\s+/, '').replace(/\s+$/, '');
      if (text === '') {
          this.word = ''
          this.result = 'Ваша надпись'
      } else {
        this.result = this.word
      }
      
    }
  }

}
</script>

